<template>
	<view class="content">
		<button size="mini" @click="clear()">清空</button>
		<text>连接状态：{{status.connected?"已连接":"未连接"}}</text>
		<view class="msg_box">
			<view v-for="item in msgs" class="msg_item">
				<view class="topic">
					{{item.topic}}
				</view>
				<view class="msg">
					{{item.msg}}
				</view>
			</view>
		</view>
		<button @click="connect()">连接</button>
	</view>
</template>

<script>

	export default {
		data() {
			return {
				msgs:getApp().globalData.msgs,
				status:getApp().globalData.status,
				options:{
					keepalive:45,
					clientId:"user_1",
					clean:true,
					connectTimeout:30000,
					username:"user_1",
					password:"abc123",
					keepalive:10
					
				},
				// #ifdef H5
				broker:"ws://192.168.199.156:8083/mqtt",
				// #endif
				// #ifdef APP-PLUS
				broker:"wx://192.168.199.156:8083/mqtt",
				// #endif
				// #ifdef MP-WEIXIN
				broker:"wss://emqx.net:8084/mqtt",
				// #endif
				
				client:null,
				status:{
					connecting:false,
					connected:false
				}
			}
		},
		onLoad() {

		},
		methods: {
			clear(){
				getApp().globalData.msgs.splice(0)
			},
			connect(){
				const mqtt = require("mqtt")
				let client = mqtt.connect(this.options.broker,this.options)
				client.on("connect",(res)=>{
					uni.showToast({
						title:"连接成功！"
					})
					console.log(res)
				})
			}
		},
		computed:{
		}
	}
</script>

<style lang="scss">
	.content{
		padding: 10rpx;
		.msg_box{
			border: 1px solid #afa178;
			padding: 5rpx;
			.msg_item{
				padding: 10rpx;
				border: 1px dotted #a66678;
				.topic{
					font-size: 30rpx;
					color: #afaf11;
				}
				.msg{
					font-size: 23rpx;
					color: #af8f11;
				}
			}
		}
	}
</style>
